// src/components/AuthGuard.jsx
import React from 'react';
import { useAuth } from '../contexts/AuthProvider';

/**
 * 
 */
const DefaultLoadingSpinner = () => (
  <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
    <div className="sm:mx-auto sm:w-full sm:max-w-md">
      <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
        <div className="flex justify-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
        </div>
        <p className="text-center text-gray-600 mt-4">...</p>
      </div>
    </div>
  </div>
);

/**
 * 
 */
const WaitingApproval = ({ userProfile, onSignOut }) => {
  const getStatusMessage = () => {
    switch (userProfile?.status) {
      case 'pending':
        return {
          title: '',
          message: '',
          icon: '',
          color: 'text-yellow-600',
          bgColor: 'bg-yellow-50',
        };
      case 'rejected':
        return {
          title: '',
          message: '',
          icon: '',
          color: 'text-red-600',
          bgColor: 'bg-red-50',
        };
      default:
        return {
          title: '',
          message: '',
          icon: '',
          color: 'text-gray-600',
          bgColor: 'bg-gray-50',
        };
    }
  };

  const statusInfo = getStatusMessage();

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-md">
        <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
          <div className="text-center">
            <div className={`mx-auto flex items-center justify-center h-16 w-16 rounded-full ${statusInfo.bgColor} mb-4`}>
              <span className="text-2xl">{statusInfo.icon}</span>
            </div>
            
            <h2 className={`text-2xl font-bold ${statusInfo.color} mb-4`}>
              {statusInfo.title}
            </h2>
            
            <p className="text-gray-600 mb-6">
              {statusInfo.message}
            </p>

            {userProfile && (
              <div className="bg-gray-50 rounded-lg p-4 mb-6 text-left">
                <h3 className="font-medium text-gray-900 mb-2"></h3>
                <div className="text-sm text-gray-600 space-y-1">
                  <p><strong></strong>{userProfile.email}</p>
                  <p><strong></strong>{new Date(userProfile.requested_at).toLocaleString('zh-CN')}</p>
                  <p><strong></strong>
                    <span className={`ml-1 px-2 py-1 rounded text-xs ${
                      userProfile.status === 'pending' ? 'bg-yellow-100 text-yellow-800' :
                      userProfile.status === 'rejected' ? 'bg-red-100 text-red-800' :
                      'bg-gray-100 text-gray-800'
                    }`}>
                      {userProfile.status === 'pending' ? '' :
                       userProfile.status === 'rejected' ? '' : ''}
                    </span>
                  </p>
                  {userProfile.notes && (
                    <p><strong></strong>{userProfile.notes}</p>
                  )}
                </div>
              </div>
            )}

            <div className="space-y-3">
              <button
                onClick={() => window.location.reload()}
                className="w-full inline-flex justify-center items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
              >
                
              </button>
              
              <button
                onClick={onSignOut}
                className="w-full inline-flex justify-center items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-gray-600 hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500"
              >
                
              </button>
            </div>

            {userProfile?.status === 'pending' && (
              <div className="mt-6 text-xs text-gray-500">
                <p>1-2</p>
                <p></p>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

/**
 * 
 */
const EmailVerificationRequired = ({ user }) => (
  <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
    <div className="sm:mx-auto sm:w-full sm:max-w-md">
      <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
        <div className="text-center">
          <div className="mx-auto flex items-center justify-center h-16 w-16 rounded-full bg-blue-50 mb-4">
            <span className="text-2xl"></span>
          </div>
          <h2 className="text-2xl font-bold text-gray-900 mb-4"></h2>
          <p className="text-gray-600 mb-4">
            
          </p>
          <p className="text-sm text-gray-500 mb-6">
            {user.email}
          </p>
          <button
            onClick={() => window.location.reload()}
            className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
);

/**
 * 
 */
const AccessDenied = () => (
  <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
    <div className="sm:mx-auto sm:w-full sm:max-w-md">
      <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
        <div className="text-center">
          <div className="mx-auto flex items-center justify-center h-16 w-16 rounded-full bg-red-50 mb-4">
            <span className="text-2xl"></span>
          </div>
          <h2 className="text-2xl font-bold text-red-600 mb-4"></h2>
          <p className="text-gray-600">
            
          </p>
        </div>
      </div>
    </div>
  </div>
);

/**
 *  - ProtectedRouteWaitingApproval
 * @param {ReactNode} children - 
 * @param {ReactNode} fallback - 
 * @param {boolean} requireApproval - 
 * @param {boolean} requireAdmin - 
 * @param {ReactNode} loadingComponent - 
 * @param {boolean} checkEmailVerification - 
 */
const AuthGuard = ({ 
  children, 
  fallback = <div></div>,
  requireApproval = true,
  requireAdmin = false,
  loadingComponent,
  checkEmailVerification = true
}) => {
  const { user, userProfile, loading, isUserApproved, isAdmin, signOut } = useAuth();

  // ===  ===
  console.log('=== AuthGuard Debug Info ===');
  console.log('loading:', loading);
  console.log('user:', user);
  console.log('userProfile:', userProfile);
  console.log('requireApproval:', requireApproval);
  console.log('fallback:', fallback);
  console.log('=== End Debug Info ===');

  // 
  if (loading) {
    console.log('=== AuthGuard: Showing loading component');
    return loadingComponent || <DefaultLoadingSpinner />;
  }

  // 
  if (!user) {
    console.log('=== AuthGuard: No user, showing fallback');
    return fallback;
  }

  // 
  if (checkEmailVerification && !user.email_confirmed_at) {
    return <EmailVerificationRequired user={user} />;
  }

  // 
  if (requireAdmin && !isAdmin()) {
    return <AccessDenied />;
  }

  // 
  if (requireApproval && !isUserApproved()) {
    return <WaitingApproval userProfile={userProfile} onSignOut={signOut} />;
  }

  // 
  return children;
};

export default AuthGuard;