// src/utils/authHelpers.js

/**
 * 
 * @param {string} email - 
 * @returns {boolean} 
 */
export const isValidEmail = (email) => {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
};

/**
 * 
 * @param {string} password - 
 * @returns {object} 
 */
export const validatePassword = (password) => {
  const errors = [];
  
  if (password.length < 6) {
    errors.push('6');
  }
  
  if (!/[A-Za-z]/.test(password)) {
    errors.push('');
  }
  
  if (!/\d/.test(password)) {
    errors.push('');
  }

  return {
    isValid: errors.length === 0,
    errors
  };
};

/**
 * 
 * @param {object} error - Supabase
 * @returns {string} 
 */
export const formatAuthError = (error) => {
  if (!error) return '';

  const errorMessages = {
    'Invalid login credentials': '',
    'Email not confirmed': '',
    'User already registered': '',
    'Password should be at least 6 characters': '6',
    'Unable to validate email address: invalid format': '',
    'Email rate limit exceeded': '',
    'Invalid email or password': '',
    'Email link is invalid or has expired': '',
    'Token has expired or is invalid': '',
    'New password should be different from the old password': ''
  };

  return errorMessages[error.message] || error.message || '';
};

/**
 * 
 * @param {object} userProfile - 
 * @param {string} requiredRole - 
 * @returns {boolean} 
 */
export const hasPermission = (userProfile, requiredRole = 'approved') => {
  if (!userProfile) return false;

  const roleHierarchy = {
    'pending': 0,
    'approved': 1,
    'admin': 2
  };

  const userLevel = roleHierarchy[userProfile.status] || 0;
  const requiredLevel = roleHierarchy[requiredRole] || 0;

  return userLevel >= requiredLevel;
};

/**
 * URL
 * @param {string} baseUrl - URL
 * @param {string} path - 
 * @returns {string} URL
 */
export const generateRedirectUrl = (baseUrl, path) => {
  const url = new URL(baseUrl);
  url.pathname = path;
  return url.toString();
};

/**
 * URL
 * @returns {object} URL
 */
export const extractAuthParams = () => {
  const urlParams = new URLSearchParams(window.location.search);
  const hash = new URLSearchParams(window.location.hash.substring(1));
  
  return {
    access_token: urlParams.get('access_token') || hash.get('access_token'),
    refresh_token: urlParams.get('refresh_token') || hash.get('refresh_token'),
    error: urlParams.get('error') || hash.get('error'),
    error_description: urlParams.get('error_description') || hash.get('error_description'),
  };
};