// src/components/admin/UserManagement.js
import React, { useState, useEffect } from 'react';
import { useAuth, createSupabaseClientFromEnv } from '../../../../auth/src';
import LoadingSpinner from '../common/LoadingSpinner';

// supabase
const supabase = createSupabaseClientFromEnv();

const UserManagement = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [processingUser, setProcessingUser] = useState(null);
  const [filter, setFilter] = useState('pending'); // pending, approved, rejected, all
  const { user: currentUser } = useAuth();

  useEffect(() => {
    fetchUsers();
  }, [filter]);

  const fetchUsers = async () => {
    try {
      setLoading(true);
      let query = supabase
        .from('user_profiles')
        .select('*')
        .order('requested_at', { ascending: false });

      if (filter !== 'all') {
        query = query.eq('status', filter);
      }

      const { data, error } = await query;

      if (error) throw error;
      setUsers(data || []);
    } catch (error) {
      console.error('Error fetching users:', error);
      alert('');
    } finally {
      setLoading(false);
    }
  };

  const handleUserAction = async (userId, action, notes = '') => {
    if (!window.confirm(`${action === 'approved' ? '' : ''}`)) {
      return;
    }

    try {
      setProcessingUser(userId);
      
      const updateData = {
        status: action,
        approved_at: action === 'approved' ? new Date().toISOString() : null,
        approved_by: currentUser.id,
        notes: notes || null,
        updated_at: new Date().toISOString(),
      };

      const { error } = await supabase
        .from('user_profiles')
        .update(updateData)
        .eq('id', userId);

      if (error) throw error;

      alert(`${action === 'approved' ? '' : ''}`);
      await fetchUsers(); // 
    } catch (error) {
      console.error('Error updating user:', error);
      alert('');
    } finally {
      setProcessingUser(null);
    }
  };

  const handleNotesSubmit = (userId, action) => {
    const notes = prompt(`${action === 'approved' ? '' : ''}`);
    if (notes !== null) { // 
      handleUserAction(userId, action, notes);
    }
  };

  const getStatusBadge = (status) => {
    const styles = {
      pending: 'bg-yellow-100 text-yellow-800',
      approved: 'bg-green-100 text-green-800',
      rejected: 'bg-red-100 text-red-800',
    };

    const labels = {
      pending: '',
      approved: '',
      rejected: '',
    };

    return (
      <span className={`px-2 py-1 text-xs font-medium rounded-full ${styles[status] || 'bg-gray-100 text-gray-800'}`}>
        {labels[status] || status}
      </span>
    );
  };

  if (loading) {
    return <LoadingSpinner message="..." />;
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900"></h1>
        <p className="mt-2 text-gray-600"></p>
      </div>

      {/*  */}
      <div className="mb-6">
        <div className="flex space-x-1 bg-gray-100 p-1 rounded-lg max-w-md">
          {[
            { key: 'pending', label: '', count: users.filter(u => u.status === 'pending').length },
            { key: 'approved', label: '', count: users.filter(u => u.status === 'approved').length },
            { key: 'rejected', label: '', count: users.filter(u => u.status === 'rejected').length },
            { key: 'all', label: '', count: users.length },
          ].map(({ key, label, count }) => (
            <button
              key={key}
              onClick={() => setFilter(key)}
              className={`px-3 py-2 text-sm font-medium rounded-md transition-colors ${
                filter === key
                  ? 'bg-white text-blue-600 shadow-sm'
                  : 'text-gray-600 hover:text-gray-900'
              }`}
            >
              {label} ({count})
            </button>
          ))}
        </div>
      </div>

      {/*  */}
      {users.length === 0 ? (
        <div className="text-center py-12">
          <p className="text-gray-500 text-lg">
            {filter === 'all' ? '' : `${filter === 'pending' ? '' : filter === 'approved' ? '' : ''}`}
          </p>
        </div>
      ) : (
        <div className="bg-white shadow overflow-hidden sm:rounded-md">
          <ul className="divide-y divide-gray-200">
            {users.map((userProfile) => (
              <li key={userProfile.id} className="px-6 py-4">
                <div className="flex items-center justify-between">
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center space-x-3">
                      <div className="flex-shrink-0">
                        <div className="h-10 w-10 rounded-full bg-gray-300 flex items-center justify-center">
                          <span className="text-sm font-medium text-gray-700">
                            {userProfile.email?.charAt(0).toUpperCase()}
                          </span>
                        </div>
                      </div>
                      <div className="flex-1 min-w-0">
                        <p className="text-sm font-medium text-gray-900 truncate">
                          {userProfile.email}
                        </p>
                        <div className="mt-1 flex items-center space-x-2">
                          {getStatusBadge(userProfile.status)}
                          <span className="text-xs text-gray-500">
                            {new Date(userProfile.requested_at).toLocaleString('zh-CN')}
                          </span>
                        </div>
                        {userProfile.notes && (
                          <p className="mt-1 text-sm text-gray-600">
                            {userProfile.notes}
                          </p>
                        )}
                        {userProfile.approved_at && (
                          <p className="mt-1 text-xs text-gray-500">
                            {new Date(userProfile.approved_at).toLocaleString('zh-CN')}
                          </p>
                        )}
                      </div>
                    </div>
                  </div>

                  {/*  */}
                  <div className="flex items-center space-x-2">
                    {userProfile.status === 'pending' && (
                      <>
                        <button
                          onClick={() => handleNotesSubmit(userProfile.id, 'approved')}
                          disabled={processingUser === userProfile.id}
                          className="inline-flex items-center px-3 py-1 border border-transparent text-xs font-medium rounded-md text-white bg-green-600 hover:bg-green-700 disabled:opacity-50"
                        >
                          {processingUser === userProfile.id ? '...' : ''}
                        </button>
                        <button
                          onClick={() => handleNotesSubmit(userProfile.id, 'rejected')}
                          disabled={processingUser === userProfile.id}
                          className="inline-flex items-center px-3 py-1 border border-transparent text-xs font-medium rounded-md text-white bg-red-600 hover:bg-red-700 disabled:opacity-50"
                        >
                          {processingUser === userProfile.id ? '...' : ''}
                        </button>
                      </>
                    )}
                    
                    {userProfile.status === 'approved' && (
                      <button
                        onClick={() => handleUserAction(userProfile.id, 'rejected', '')}
                        disabled={processingUser === userProfile.id}
                        className="inline-flex items-center px-3 py-1 border border-gray-300 text-xs font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 disabled:opacity-50"
                      >
                        
                      </button>
                    )}

                    {userProfile.status === 'rejected' && (
                      <button
                        onClick={() => handleUserAction(userProfile.id, 'approved', '')}
                        disabled={processingUser === userProfile.id}
                        className="inline-flex items-center px-3 py-1 border border-transparent text-xs font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 disabled:opacity-50"
                      >
                        
                      </button>
                    )}
                  </div>
                </div>
              </li>
            ))}
          </ul>
        </div>
      )}

      {/*  */}
      <div className="mt-6 text-center">
        <button
          onClick={fetchUsers}
          className="inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50"
        >
          
        </button>
      </div>
    </div>
  );
};

export default UserManagement;