// src/components/Login.jsx
import React, { useState } from 'react';
import { useAuth } from '../contexts/AuthProvider';
import { isValidEmail, validatePassword, formatAuthError } from '../utils/authHelpers';

const Login = ({ 
  onSuccess,
  redirectTo,
  className = "",
  showTitle = true 
}) => {
  const [isLogin, setIsLogin] = useState(true);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [showResetPassword, setShowResetPassword] = useState(false);

  const { signIn, signUp, resetPassword } = useAuth();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage('');

    // 
    if (!email || !password) {
      setMessage('');
      setLoading(false);
      return;
    }

    if (!isValidEmail(email)) {
      setMessage('');
      setLoading(false);
      return;
    }

    if (!isLogin) {
      if (password !== confirmPassword) {
        setMessage('');
        setLoading(false);
        return;
      }

      const passwordValidation = validatePassword(password);
      if (!passwordValidation.isValid) {
        setMessage(passwordValidation.errors.join(''));
        setLoading(false);
        return;
      }
    }

    try {
      if (isLogin) {
        // 
        const { error } = await signIn(email, password);
        if (error) {
          setMessage(formatAuthError(error));
        } else {
          onSuccess?.();
        }
      } else {
        // 
        const { data, error } = await signUp(email, password);
        if (error) {
          setMessage(formatAuthError(error));
        } else if (data.user) {
          setMessage('');
        }
      }
    } catch (error) {
      setMessage(`: ${error.message}`);
    }

    setLoading(false);
  };

  const handleResetPassword = async (e) => {
    e.preventDefault();
    if (!email) {
      setMessage('');
      return;
    }

    if (!isValidEmail(email)) {
      setMessage('');
      return;
    }

    setLoading(true);
    const { error } = await resetPassword(email);
    
    if (error) {
      setMessage(formatAuthError(error));
    } else {
      setMessage('');
    }
    
    setLoading(false);
    setShowResetPassword(false);
  };

  return (
    <div className={`min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8 ${className}`}>
      {showTitle && (
        <div className="sm:mx-auto sm:w-full sm:max-w-md">
          <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
            {showResetPassword ? '' : isLogin ? '' : ''}
          </h2>
          {!showResetPassword && (
            <p className="mt-2 text-center text-sm text-gray-600">
              {isLogin ? '' : ''}
              <button
                type="button"
                onClick={() => {
                  setIsLogin(!isLogin);
                  setMessage('');
                }}
                className="font-medium text-blue-600 hover:text-blue-500 ml-1"
              >
                {isLogin ? '' : ''}
              </button>
            </p>
          )}
        </div>
      )}

      <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
        <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
          {showResetPassword ? (
            // 
            <form onSubmit={handleResetPassword} className="space-y-6">
              <div>
                <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                  
                </label>
                <div className="mt-1">
                  <input
                    id="email"
                    name="email"
                    type="email"
                    autoComplete="email"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    placeholder=""
                  />
                </div>
              </div>

              <div>
                <button
                  type="submit"
                  disabled={loading}
                  className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50"
                >
                  {loading ? '...' : ''}
                </button>
              </div>

              <div className="text-center">
                <button
                  type="button"
                  onClick={() => {
                    setShowResetPassword(false);
                    setMessage('');
                  }}
                  className="text-sm text-blue-600 hover:text-blue-500"
                >
                  
                </button>
              </div>
            </form>
          ) : (
            // /
            <form onSubmit={handleSubmit} className="space-y-6">
              <div>
                <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                   *
                </label>
                <div className="mt-1">
                  <input
                    id="email"
                    name="email"
                    type="email"
                    autoComplete="email"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    placeholder=""
                  />
                </div>
              </div>

              <div>
                <label htmlFor="password" className="block text-sm font-medium text-gray-700">
                   *
                </label>
                <div className="mt-1">
                  <input
                    id="password"
                    name="password"
                    type="password"
                    autoComplete={isLogin ? "current-password" : "new-password"}
                    required
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    placeholder={isLogin ? "" : "6"}
                  />
                </div>
              </div>

              {!isLogin && (
                <div>
                  <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700">
                     *
                  </label>
                  <div className="mt-1">
                    <input
                      id="confirmPassword"
                      name="confirmPassword"
                      type="password"
                      autoComplete="new-password"
                      required
                      value={confirmPassword}
                      onChange={(e) => setConfirmPassword(e.target.value)}
                      className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                      placeholder=""
                    />
                  </div>
                </div>
              )}

              {message && (
                <div className={`text-sm p-3 rounded ${message.includes('') || message.includes('') 
                  ? 'bg-green-100 text-green-800' 
                  : 'bg-red-100 text-red-800'
                }`}>
                  {message}
                </div>
              )}

              <div>
                <button
                  type="submit"
                  disabled={loading}
                  className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50"
                >
                  {loading ? (isLogin ? '...' : '...') : (isLogin ? '' : '')}
                </button>
              </div>

              {isLogin && (
                <div className="text-center">
                  <button
                    type="button"
                    onClick={() => {
                      setShowResetPassword(true);
                      setMessage('');
                    }}
                    className="text-sm text-blue-600 hover:text-blue-500"
                  >
                    
                  </button>
                </div>
              )}

              {!isLogin && (
                <div className="text-xs text-gray-500 text-center">
                  <br />
                  
                </div>
              )}
            </form>
          )}
        </div>
      </div>
    </div>
  );
};

export default Login;